html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: #657180;
  font-size: 16px;
}

.daily-menu {
  width: 200px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  background: #f5f7f9;
}

.daily-menu-item {
  font-size: 18px;
  text-align: center;
  padding: 10px 0;
  cursor: pointer;
  border-right: 2px solid transparent;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.daily-menu-item:hover {
  background: #e3e8ee;
}

.daily-menu-item.on {
  color: white;
  background: #06ade8;
}

.daily-menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.daily-menu ul li{
  height: 35px;
  margin-left: 25px;
  padding-left: 10px;
  cursor: pointer;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.daily-menu ul li a{
  display: block;
  height: 100%;
}

.daily-menu ul li a .imgSec{
  float: left;
  height: 25px;
  width: 25px;
  margin-top: 5px;
  border-radius: 5px;
  overflow: hidden;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.imgSec img{
  height: 100%;
}

.daily-menu ul li a span {
  margin-left: 10px;
  display: inline-block;
  height: 100%;
  width: 105px;
  line-height: 35px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.daily-menu ul li:hover{
  background-color: #e3e8ee;
}

.daily-menu ul li.on {
  height: 45px;
  border-left: 4px solid #06ade8;
  background-color: #d4dce6;
  margin-left: 0px;
}

.daily-menu ul li.on .imgSec{
  width: 35px;
  height: 35px;
}

.daily-menu ul li.on span{
  width: 120px;
  color: #5a5a5a;
  line-height: 45px;
  font-weight: 900;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.daily-list {
  width: 300px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 200px;
  overflow: auto;
  border-right: 1px solid #d7dde4;
  background-color: white;
}

.daily-list>div{
  padding-top: 10px;
}

.daily-list>div:first-child{
  padding: 0;
}

.daily-list .firsttime {
  text-align: center;
  margin-top: 12px;
  font-size: 12px;
  color:  #8c8c8c;
}

.daily-item {
  display: block;
  color: inherit;
  text-decoration: none;
  padding: 16px;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.daily-item:hover {
  color: #657180;
  background: #e3e8ee;
}

.daily-img {
  width: 80px;
  height: 80px;
  float: left;
}

.daily-img img {
  width: 100%;
  height: 100%;
  border-radius: 3px;
}

.daily-title {
  padding: 10px 5px 10px 90px;
}

.daily-title.noImg {
  padding-left: 5px;
}

.daily-article {
  margin-left: 500px;
  padding: 20px;
}

.daily-date {
  text-align: center;
  padding: 10px 0;
  background: #ececec54;
  font-weight: bold;
  font-size: 14px;
}

.daily-article-title {
  font-size: 28px;
  font-weight: bold;
  color: #222;
  padding: 10px 0;
}

.view-more a {
  display: block;
  cursor: pointer;
  background: #f5f7f9;
  text-align: center;
  color: inherit;
  text-decoration: none;
  padding: 4px 0;
  border-radius: 3px;
}

.daily-comments {
  margin: 10px 0;
}

.daily-comments span {
  display: block;
  margin: 10px 0;
  font-size: 20px;
}

.daily-comment {
  overflow: hidden;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px dashed #e3e8ee;
}

.daily-comment-avatar {
  width: 50px;
  height: 50px;
  float: left;
  border-radius: 50%;
  overflow: hidden;
}

.daily-comment-avatar img {
  width: 100%;
  height: 100%;
  border-radius: 3px;
}

.daily-comment-content {
  margin-left: 65px;
}

.daily-comment-time {
  color: #9ea7b4;
  font-size: 14px;
  margin-top: 5px;
}

.daily-comment-text {
  margin-top: 10px;
}

.answer {
  margin: 30px 0px;
  border-bottom: 1px dashed #acc1da;
}

.meta img{
  border-radius: 50%;
}

.meta span{
  vertical-align: 10px;
}

.meta .author{
  margin-left: 10px;
}

.meta .bio{
  color: #8c8c8c;
  font-size: 14px;
}

.click {
  background: #eaeaea;
  color: dimgrey;
}
